@import '../../../styles/templarios.abstracts';

:host {
  position: relative;
  padding: 0px;
  width: 100%;
  max-width: 1200px;
  margin: 0px auto;

  .tp-cartao-resposta-lista {
    &__container {
      display: grid;
      justify-content: center;
      grid-template-columns: repeat(auto-fit, 10vw);

      @include breakpoint-down('xl') {
        grid-template-columns: repeat(auto-fit, 10vw);
      }

      @include breakpoint-down('lg') {
        grid-template-columns: repeat(auto-fit, 13vw);
      }

      @include breakpoint-down('md') {
        grid-template-columns: repeat(auto-fit, 14vw);
      }

      @include breakpoint-down('sm') {
        grid-template-columns: repeat(auto-fit, 17vw);
      }

      @include breakpoint-down('xs') {
        grid-template-columns: repeat(auto-fit, 19vw);
      }

      &--flex {
        display: flex;
        flex-wrap: wrap;
      }
    }
  }
}
