import { Component, Host, Prop, h } from '@stencil/core';

@Component({
  tag: 'tp-cartao-resposta-lista',
  styleUrl: 'cartao-resposta-lista.scss',
  shadow: true,
})
export class TpCartaoRespostaLista {
  /**
   * Altera o layout do componente de grid para flex.
   */
  @Prop({ reflect: true }) flex = false;

  render() {
    const { flex } = this;

    return (
      <Host class="tp-cartao-resposta-lista">
        <div
          class={{
            'tp-cartao-resposta-lista__container': true,
            'tp-cartao-resposta-lista__container--flex': flex,
          }}
        >
          <slot></slot>
        </div>
      </Host>
    );
  }
}
